const initDropdown = () => {
  const guaranteeBtn = document.querySelector('.js-guarantee-btn');
  const guarantee = document.querySelector('.js-guarantee');

  if (guaranteeBtn) {

    guaranteeBtn.addEventListener('click', function (evt) {
      evt.preventDefault();
      guarantee.classList.toggle('visually-hidden');
    });

  }

  const cards = document.querySelectorAll('.js-card');

  if (cards) {

    cards.forEach((card) => {
      const btn = card.querySelector('.js-btn-dropdown');
      const elements = card.querySelectorAll('.js-change-content');

      btn.addEventListener('click', function () {

        elements.forEach((el) => {
          let className = el.dataset.dropdown;
          el.classList.toggle(className);
        });

      });
    });

  }

};

export {initDropdown};
