const initForms = () => {
  const forms = document.querySelectorAll('.form');
  const fields = document.querySelectorAll('.form__item');

  fields.forEach((field) => {
    field.addEventListener('input', (evt) => {

      if (evt.target.value.length > 0 && !field.classList.contains('filled')) {
        field.classList.add('filled');
      } else if (evt.target.value.length === 0 && field.classList.contains('filled')) {
        field.classList.remove('filled');
      }
    });
  });

  forms.forEach((form) => {
    const sumbitBtn = form.querySelector('.form__btns button');
    const formFields = form.querySelectorAll('.form__item');

    if (sumbitBtn) {
      sumbitBtn.addEventListener('click', () => {
        formFields.forEach((field) => {
          if (!field.classList.contains('validated')) {
            field.classList.add('validated');
          }
        });
      });
    }
  });
};

export {initForms};
